.navbar{
    position: fixed;
    top: 0;
    background-color:#111;
    width: 100%;
    height: 30px;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;
   
}

.logo{color: aliceblues;
    position: fixed;
    left: 20px;
    width: 120px;
    
    
}
.avatar{
    position: fixed;
    right: 20px;
    width: 30px;
}
*{
    margin: 0;
    padding: 0;
}
body{
    background-color: #111;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.container{
   margin-top: 100px;}
   h1{
    text-align: center;
    
 
   }
   .thumbNail{
   display: flex;
   flex-direction: column;
    padding:10px;
   align-items: center;
   margin-bottom: 5px;
   border-bottom: 2px solid gold;
   padding: 10px;
   justify-content: center;
  
   }
   .btn{
    background-color: blue;
    color: white;
    padding: 5px;
    border-radius: 7px;
    border-color: black;
    margin-top: 10px;
    cursor: pointer;
    
    
   }
   h1,.moviedescription{
   
    color: white;
   }
   .mylistImage{
      width: 400px;
   height: auto;
   }
   .mylistDescription{
      color: white;
      font-size: large;
      text-align: left;
      max-width: 390px;
   }
   h3{
      margin: 6px;
      text-align: center;
      color:gold;
      border-bottom: 2px solid white;
   }
.rows{
    padding: 20px;
    color: #fff;
    
   
}
.posters{
    padding: 20px;
    display: flex;
    overflow-x:scroll ;
    overflow-y: hidden;
    
}


.posterImage{
    max-height: 250px;
    margin-right: 10px;
    cursor: pointer;
}

.posterImage-small{
    max-height: 150px;
    margin-right: 10px;
    cursor: pointer;
}

.posters::-webkit-scrollbar{
    display: none;
}
.posterImage-small:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}.posterImage:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
.youtube{
    padding:2rem;
   
}
.closeBtn{
    color:white;
    background-color: red;
    border: none;
    padding:10px;
    font-weight: 700;
    border-radius: 10px;
    cursor: pointer;

}
.btnDiv{
    display: flex;
    justify-content: center;
    margin: 3px;
   
}
.myListBtn{
    color:white;
    background-color: red;
    padding: 5px;
    border-radius: 8px;
    box-shadow: #fff;
    width:50%;
    cursor: pointer;
    margin-top: 5px;
}
.moviesPosters{
text-align: center;
}
.removeMyListBtn{
    background-color: white;
    padding: 5px;
    border-radius: 8px;
    font-weight: 700;
    margin-top: 5px;
    font-weight: 700;
}
.banner{
   
background-size: cover;
height: 480px;
color:white;
}
.content{
    padding-top: 140px;
    height: 190px;
    padding-left: 15px;
}
.title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.button{
    color: white;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-right: 2rem;
    padding-left: 2rem;
    padding-top: 0.5rem;
    padding-bottom:0.5rem;
    background-color: rgba(51, 51, 51, 0.5);
    margin-right: 1rem;
    cursor: pointer;
}
.button:hover{
    color: black;
    background-color: white;
}
.description{
    width:45rem;
    line-height: 1.3rem;
    padding-top: 1rem;
    font-size: 1rem;
    height: 80px;
    max-width: 360px;

}
.fade-bottom{
    height: 9.4rem;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,.61),#111);
}
.cover-image-youtube{
height: 500px;
margin-top: 10px;
padding: 40px;


}
